@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding-left: 40px;
  padding-right: 40px;
}
@media (max-width: 1023px) {
  .container {
    padding-left: 45px;
    padding-right: 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}
.container--description {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .container--description {
    display: block;
  }
}
.container--features {
  padding-top: 88px;
  padding-bottom: 106px;
}
@media (max-width: 1023px) {
  .container--features {
    padding-top: 68px;
    padding-bottom: 84px;
  }
}
@media (max-width: 767px) {
  .container--features {
    padding-top: 38px;
    padding-bottom: 49px;
  }
}
.container--footer {
  display: flex;
}
@media (max-width: 767px) {
  .container--footer {
    flex-direction: column;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  padding-top: 35px;
  color: #f9fbfd;
  background-image: url("../img/main-screen-bg-desktop.jpg");
  background-color: #011c40;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 0 0;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .header {
    background-image: url("../img/main-screen-bg-desktop@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .header {
    padding-top: 20px;
    background-image: url("../img/main-screen-bg-tablet.jpg");
    box-shadow: none;
  }
}
@media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
  .header {
    background-image: url("../img/main-screen-bg-tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .header {
    position: relative;
    background-image: url("../img/main-screen-bg-phone.jpg");
  }
}
@media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
  .header {
    background-image: url("../img/main-screen-bg-phone@2x.jpg");
  }
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.header__logo-image {
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .header__logo-image {
    margin-top: 10px;
  }
}
@media (max-width: 767px) {
  .header__logo-image {
    margin-top: 0;
  }
}

.header__toggle {
  display: none;
}
@media (hover: hover) {
  .header__toggle:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.header__toggle.focus-visible:focus {
  opacity: 0.8;
}
@media (max-width: 767px) {
  .header__toggle {
    display: block;
    position: absolute;
    box-sizing: content-box;
    top: 13px;
    right: 5px;
    width: 24px;
    height: 16px;
    padding: 10px;
    cursor: pointer;
    border: none;
    background-color: transparent;
  }
}

.header__item {
  margin-top: 8px;
  margin-bottom: 16px;
}
@media (max-width: 767px) {
  .header__item {
    margin-bottom: 9px;
  }
}

.header__link {
  margin-left: 33px;
  padding-top: 8px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (hover: hover) {
  .header__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.header__link.focus-visible:focus {
  opacity: 0.8;
}
@media (max-width: 1023px) {
  .header__link {
    margin-left: 24px;
    font-size: 14px;
    line-height: 1.2;
  }
}
@media (max-width: 767px) {
  .header__link {
    display: block;
    padding: 10px;
    margin-left: 0;
    font-size: 18px;
    text-shadow: none;
  }
}

@media (max-width: 767px) {
  .header__wrapper--opened {
    display: block;
    position: absolute;
    top: 0;
    height: 100vh;
    left: 35px;
    right: 0;
    z-index: 1;
    padding: 20px 15px;
    background-color: #f9fbfd;
    color: #011c40;
  }
}

@media (max-width: 767px) {
  .header__wrapper--opened .header__list {
    display: block;
    margin-top: 26px;
  }
}

.header__wrapper--opened .header__toggle::before, .header__wrapper--opened .header__toggle::after {
  content: "";
  position: absolute;
  left: 18px;
  width: 18px;
  height: 2px;
  background-color: #011c40;
}
.header__wrapper--opened .header__toggle::before {
  transform: rotate(45deg);
}
.header__wrapper--opened .header__toggle::after {
  transform: rotate(-45deg);
}

@media (max-width: 767px) {
  .header__wrapper--closed .header__toggle::before {
    content: "";
    position: absolute;
    top: 10px;
    left: 10px;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
    box-shadow: 0 7px 0 0 #f9fbfd, 0 14px 0 0 #f9fbfd;
  }
}

@media (max-width: 767px) {
  .header__wrapper--closed .header__list {
    display: none;
  }
}

@media (max-width: 767px) {
  .no-js .header__toggle {
    display: none;
  }
}

@media (max-width: 767px) {
  .no-js .header__wrapper {
    display: block;
    position: static;
  }
}

@media (max-width: 767px) {
  .no-js .header__list {
    display: block;
    height: auto;
  }
}

.logo {
  color: inherit;
}

.logo__image {
  max-width: none;
}
@media (max-width: 1023px) {
  .logo__image {
    width: 38px;
