.features {
  position: relative;
}
.features::before, .features::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}
.features::after {
  background-image: url("../img/features-bg-desktop.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0.1;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .features::after {
    background-image: url("../img/features-bg-desktop@2x.jpg");
  }
}
.features::before {
  background-color: #e3ecf6;
}
@media (max-width: 1023px) {
  .features::after {
    background-image: url("../img/features-bg-tablet.jpg");
  }
}
