  }
}

@media (max-width: 1023px) {
  .features__item--zodiac svg {
    width: 36px;
    height: 26px;
  }
}

@media (max-width: 1023px) {
  .features__item--zodiac h3 {
    margin-bottom: 20px;
  }
}

.footer {
