  background-color: #011c40;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .footer {
    padding-top: 40px;
  }
}
@media (max-width: 767px) {
  .footer {
    padding-top: 30px;
    padding-bottom: 30px;
  }
}

